.frado-cta{
    &-section{
        padding: 80px 0;
        border-radius: 20px;
        background-color: var(--accent-bg);
        @include max (767px){
            padding: 60px 0;
        }
    }
    &-title{
        max-width: 700px;
        margin: 0 auto;
        text-align: center;
        @include max (991px){
            max-width: 550px;
        }
    }
    // end
}

