.frado-blog{
    &-wrap{
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color: var(--light-color);
        box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        @include max (767px){
            padding: 30px 24px;
        }
        &.wrap2{
            margin: 0 12px 24px;
        }
        &:hover{
            box-shadow: 0px 4px 50px 0px rgba(0, 0, 0, 0.25);
            .frado-blog-thumb img{
                transform: scale(1.1);
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 20px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 20px;
        }
    }
    &-tag{
        position: absolute;
        top: 24px;
        right: 24px;
        a{
            color: var(--light-color);
            padding: 8px 35px;
            background-color: rgba(24, 29, 20, 0.5);
            border-radius: 50px;
            &:hover{
                color: var(--heading-color);
                background-color: var(--light-color);
            }
        }
    }
    &-content{
        padding: 24px 20px 0 20px;
        h4{
            padding-bottom: 15px;
        }
        @include max (767px){
            padding: 24px 0;
        }
        &.content2{
            padding: 24px 0 0 0;
        }
    }
    // add single blog
    &-d-wrap{
        max-width: 1070px;
        margin: 0 auto;
    }
    &-d-meta{
        padding-top: 24px;
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    color: var(--heading-color);
                    transition: all .4s;
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    &:hover{
                        color: var(--accent-color);
                        svg path{
                            fill: var(--accent-color);
                        }
                    }
                    svg path{
                        margin-right: 10px;
                        transition: all 0.4s;
                    }
                }
            }
        }
    }
    &-d-content{
        padding-right: 50px;
        @include max (1199px){
            padding-right: 20px;
        }
        @include max (991px){
            padding-right: 0;
        }
        h3, h4{
            padding-bottom: 15px;
        }
        ul{
            li{
                position: relative;
                padding-left: 15px;
                margin-bottom: 5px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    top: 15px;
                    left: 0;
                    width: 5px;
                    height: 5px;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
            }
        }
    }
    &-d-author-wraper{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: 80px;
        flex-wrap: wrap;
        gap: 15px;
        padding-top: 60px;
        @include max (1199px){
            padding-right: 0;
        }
        @include max (991px){
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    &-d-author-wrap{
        display: flex;
        align-items: center;
        gap: 12px;
    }
    &-d-author-data{
        p{
            margin-bottom: 5px;
        }
        span{
            font-size: 18px;
            line-height: 25px;
        }
    }
    &-d-author-info{
        p{
            margin-bottom: 5px;
        }
    }
    &-sidebar{
        background-color: var(--heading-bg);
        border-radius: 20px;
        padding: 30px;
        position: sticky;
        top: 120px;
        margin-left: 30px;
        @include max (991px){
            margin-left: 0;
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
        .frado-main-field input{
            margin-bottom: 24px;
        }
        h4{
            padding-bottom: 15px;
            color: var(--light-color);
        }
        p{
            color: var(--light-color);
        }
    }
}
blockquote {
    margin-top: 24px;
    border-radius: 10px;
    padding: 24px;
    position: relative;
    margin-bottom: 0;
    box-shadow: 0px 4px 50px 0px rgba(0, 0, 0, 0.25);
    p{
        margin-bottom: 10px;
    }
    &::before{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 5px;
        height: 100%;
        border-radius: 10px 0 0 10px;
        background-color: var(--accent-bg);
    }
}
.pt-24{
    padding-top: 24px;
}
.mr-70{
    margin-right: 70px;
    @include max (1199px){
        margin-right: 20px;
    }
    @include max (991px){
        margin-right: 0;
        margin-bottom: 40px;
    }
    @include max (767px){
        margin-bottom: 30px;
    }
}
.mt-80{
    margin-top: 80px;
    @include max (991px){
        margin-top: 60px;
    }
    @include max (767px){
        margin-top: 40px;
    }
}