.frado-pricing{
    &-table{
        padding-top: 80px;
        @include max (1349px){
            overflow-x: auto;
        }
        @include max (767px){
            padding-top: 60px;
        }
    }
    &-wrap{
        border-radius: 20px;
        padding: 30px;
        box-shadow: 0 4px 50px rgba(0, 0, 0, 0.05);
        text-align: center;
        transition: all 0.7s;
        
        &:hover{
            background-color: var(--heading-bg);
            .sub-title{
                color: var(--light-color);
            }
            .frado-pricing-price{
                color: var(--light-color);
            }
            .frado-pricing-text{
                color: var(--light-color);
            }
            .pricing-btn{
                background-color: var(--accent-color);
            }
        }
    }
    &-header{
        padding-bottom: 10px;
        span.sub-title{
            font-size: 28px;
            font-weight: 700;
            line-height: 30px;
            transition: all 0.4s;
        }
    }
    &-text{
        transition: all 0.4s;
    }
    &-price{
        gap: 5px;
        display: flex;
        transition: all 0.4s;
        justify-content: center;
        align-items: flex-end;
    }
}
.frado-price-value{
    font-size: 70px;
    line-height: 1em;
}
.toggle-btn{
    margin: 0 15px;
}

tr {
    text-align: center;
    display: grid;
    gap: 24px;
    margin-bottom: 24px;
    grid-template-columns: repeat(4, 1fr);
}
.table-header{
    th{
        text-align: left;
        span{
            font-size: 35px;
            line-height: 1.2em;
            @include max (575px){
                font-size: 30px;
            }
        }
    }
}
.title-date{
    margin-top: 10px;
    font-weight: 400;
}

.table-sub-title{
    margin: 0;
    border-top: 1px solid rgba(24, 29, 20, 0.1);
    th {
        padding: 24px 0;
        ul{
            li{
                font-size: 26px;
                line-height: 36px;
                font-weight: 400;
                display: inline-block;
                img{
                    margin-right: 10px;
                }
            }
        }
        p{
            text-align: left;
            font-weight: 700;
        }
    }
}



// switcher btn

.pricing-btn {
    display: flex;
    align-items: center;
    label {
        color: var(--heading-color);
        font-weight: 700;
    }
}

[data-pricing-dynamic][data-value-active="yearly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-yearly);
  }
 
  
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] span {
  left: calc(100% - 33px);
  }
  [data-pricing-dynamic][data-value-active="monthly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-monthly);
  }
  .dynamic-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .static-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .pricing-btn .btn-toggle {
    position: relative;
    width: 70px !important;
    height: 35px;
    border-radius: 50px;
    border: none;
    margin: 0;
    padding: 0!important;
    cursor: pointer;
    background-size: 25px;
    border: none!important;
    background-position: center right 5px;
    border: none !important;
    background-position: center right 5px;
    background-color: var(--accent-color);
    background-image: url(../images/pricing/switcher-btn.svg) !important;
}
 
.form-check-input:focus {
    border-color: none;
    outline: 0;
    box-shadow:none;
}
.pricing-btn .btn-toggle:checked {
    background-position: center left 5px;
    background-image: url(../images/pricing/switcher-btn.svg) !important;
}
