.frado-hero{
  &-section{
    padding: 195px 0 130px;
    position: relative;
    z-index: 0;
    @include max (1199px){
      padding: 190px 0 130px;
    }
    @include max (991px){
      padding: 170px 0 100px;
    }
    @include max (767px){
      padding: 130px 0 80px;
    }
  }
  &-content{
    position: relative;
    p{
      font-size: 24px;
      line-height: 32px;
      max-width: 700px;
    }
  }
  &-thumb{
    @include max (991px){
      padding-top: 40px;
    }
    @include max (767px){
      padding-top: 30px;
    }
  }
  // end one
  &-section2{
    padding: 195px 0 0;
    position: relative;
    z-index: 0;
    @include max (1199px){
      padding: 190px 0 0;
    }
    @include max (991px){
      padding: 190px 0 0;
    }
    @include max (767px){
      padding: 130px 0 0;
    }
  }
  &-content2{
    text-align: center;
    position: relative;
    p{
      font-size: 24px;
      line-height: 32px;
      max-width: 1120px;
      margin: 0 auto;
    }
  }
  &-btn-wrap{
    display: flex;
    gap: 24px;
    flex-wrap: wrap;
    justify-content: center;
  }
  &-thumb2{
    padding-top: 100px;
    @include max (991px){
      padding-top: 80px;
    }
    @include max (767px){
      padding-top: 60px;
    }
    img{
      border-radius: 20px;
    }
  }
}

.frado_screenfix_right {
  margin-right: calc((-100vw + 100%) / 6);
  max-width: 1610px;
  @include max (1399px){
    margin-right: calc((-100vw + 100%) / 3);
  }
  @include max (991px){
    max-width: 100%;
    margin-right: auto;
  }
}