.frado-iconbox{
    &-wraper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        @include max (1199px){
            gap: 24px;
            grid-template-columns: repeat(2, 1fr);
        }
        @include max (767px){
            grid-template-columns: repeat(1, 1fr);
        }
    }
    &-item{
        padding: 30px 24px;
        transition: all .7s;
        background-color: var(--light-color);
        border: 1px solid var(--heading-color);
        @include max (1199px){
            border-radius: 20px;
        }
        &:hover{
            background-color: var(--heading-bg);
            .frado-iconbox-icon{
                path{
                    fill: var(--accent-color);
                }
            }
            .frado-iconbox-content{
                h4,p{
                    color: var(--light-color);
                }
            }
        }
        &:first-child{
            border-radius: 20px 0 0 20px;
            @include max (1199px){
                border-radius: 20px;
            }
        }
        &:last-child{
            border-radius: 0 20px 20px 0;
            @include max (1199px){
                border-radius: 20px;
            }
        }
    }
    &-icon{
        padding-bottom: 50px;
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        path{
            transition: all 0.4s;
        }
    }
    &-content{
        h4{
            padding-bottom: 50px;
            transition: all 0.4s;
            @include max (991px){
                padding-bottom: 40px;
            }
            @include max (767px){
                padding-bottom: 30px;
            }
        }
        p{
            transition: all 0.4s;
        }
    }
    // end one
    &-wrap{
        padding: 30px;
        border-radius: 20px;
        margin: 0 15px;
        background-color: var(--light-color);
        border: 1px solid var(--heading-color);
        @include max (767px){
            padding: 30px 20px;
        }
    }
    &-author-wrap{
        display: flex;
        gap: 12px;
        align-items: center;
        p{
            margin-bottom: 2px;
        }
        span{
            font-size: 18px;
            line-height: 25px;
        }
    }
    &-title{
        h4{
            padding-bottom: 12px;
        }
    }
    // end twoo
    &-wraper3{
        display: flex;
        gap: 50px;
        padding-right: 30px;
        align-items: center;
        @include max (1199px){
            gap: 20px;
            padding-right: 15px;
        }
        @include max (991px){
            padding-right: 0;
        }
        @include max (767px){
            display: block;
        }
    }
    &-wrap3{
        &.wr3{
            @include max (767px){
                margin-bottom: 30px;
            }
        }
    }
    &-content3{
        h4{
            padding: 24px 0;
        }
    }
}