input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  color: rgba(243, 238, 227, 0.5)!important;
  border-radius: 0;
  background-color: transparent;
  padding: 17px 24px;
  border: none;
  transition: all 0.4s;
}
textarea {
  height: 250px;
  resize: none;
  
  @include max(767px) {
    height: 150px;
  }
}
.frado-main{
   &-field{
      margin-bottom: 24px;
      input{
         border-radius: 50px;
         border: 1px solid rgba(243, 238, 227, 0.5);
         font-size: 20px;
         line-height: 30px;
      }
      &-textarea{
         border-radius: 20px;
         margin-bottom: 24px;
         font-size: 20px;
         line-height: 30px;
         border: 1px solid rgba(243, 238, 227, 0.5);
         
      }
   }
}

