.frado-footer{
  &-section{
    padding: 130px 0 0;
    position: relative;
    @include max (991px){
      padding: 80px 0 0;
    }
    @include max (767px){
      padding: 60px 0 0;
    }
  }
  &-section2{
    padding: 100px 0;
    position: relative;
    @include max (991px){
      padding: 80px 0;
    }
    @include max (767px){
      padding: 60px 0;
    }
  }
  &-top{
    max-width: 700px;
    text-align: center;
    margin: 0 auto;
    h2{
      color: var(--light-color);
    }
    p{
      color: var(--light-color);
    }
    &-btn{
      padding-bottom: 80px;
      @include max (991px){
        padding-bottom: 60px;
      }
    }
  }
  &-line{
    padding-bottom: 100px;
    @include max (991px){
      padding-bottom: 80px;
    }
    @include max (767px){
      padding-bottom: 60px;
    }
  }
  &-one{
    padding-bottom: 100px;
    @include max (991px){
      padding-bottom: 80px;
    }
    @include max (767px){
      padding-bottom: 60px;
    }
  }
  &-menu{
    @include max (767px){
      margin-bottom: 30px;
    }
    h4{
      color: var(--light-color);
      margin-bottom: 24px;
      @include max (767px){
        margin-bottom: 18px;
      }
     }
    ul{
      li{
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          position: relative;
          transition: all 0.4s;
          color: var(--light-color);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--light-color);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      h4{
        color: var(--heading-color);
      }
      ul{
        li{
          a{
            color: var(--heading-color);
            &::before{
              background-color: var(--heading-color);
            }
          }
        }
      }
    }
  }
  &-textarea{
    padding-right: 30px;
    @include max (1199px){
      margin-bottom: 30px;
      max-width: 400px;
    }
    img{
      padding-bottom: 24px;
    }
    p{
      color: var(--light-color);
      margin-bottom: 24px;
    }
    &.textarea2{
      p{
        color: var(--heading-color);
      }
    }
  }
  &-bottom-text{
    p{
      font-size: 18px;
      line-height: 25px;
    }
  }
}
.pl-30{
  padding-left: 30px;
  @include max (1399px){
    padding-left: 50px;
  }
  @include max (1199px){
    padding-left: 0;
    padding-top: 40px;
  }
}
.pll-50{
  padding-left: 50px;
  @include max (1399px){
    padding-left: 50px;
  }
  @include max (1199px){
    padding-left: 100px;
  }
  @include max (767px){
    padding-left: 0;
  }

}
.pl-80{
  padding-left: 80px;
  @include max (1199px){
    padding-left: 0;
  }
}
.pl-150{
  padding-left: 150px;
  @include max (991px){
    padding-left: 110px;
  }
  @include max (767px){
    padding-left: 0;
  }
}
