.frado-career{
    &-wrap{
        padding: 50px;
        border-radius: 20px;
        margin-bottom: 24px;
        background-color: var(--light-color);
        box-shadow:0 4px 50px rgba(0, 0, 0, 0.05);
    }
    &-title{
        padding-bottom: 24px;
        h4{
            padding-bottom: 15px;
        }
    }
    &-content{
        ul{
            li{
                font-weight: 500;
                display: inline-block;
                margin-right: 24px;
                &:last-child{
                    margin-right: 0;
                }
                img{
                    margin-right: 12px;
                    margin-top: -5px;
                }
            }
        }
    }
}