.frado-listicon{
    &-wrap{
        ul{
            li{
                margin-bottom: 15px;
                position: relative;
                padding-left: 45px;
                color: var(--heading-color);
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    position: absolute;
                    top: 4px;
                    left: 0;
                }
            }
        }
       
    }
}
