.frado-contact{
    &-main-box {
        border-radius: 20px;
        padding: 50px;
        box-shadow: 0 4px 50px rgba(0, 0, 0, 0.05);
        
        @include max (991px){
            padding: 30px;
        }
        @include max (767px){
            padding: 30px 24px;
            margin-top: 30px;
        }
    }
    &-info-wrap{
        display: flex;
        gap: 24px;
        padding-bottom: 80px;
        &:last-child{
            padding-bottom: 0;
        }
        @include max (991px){
            padding-bottom: 60px;
        }
        @include max (767px){
            padding-bottom: 40px;
        }
        h4{
            padding-bottom: 15px;
        }
        a{
            font-weight: 500;
            display: block;
            color: var(--heading-color);
        }
        span{
            font-weight: 500;
        }
    }
    &-info-icon{
        flex-shrink: 0;
    }
    &-box{
        margin-left: 80px;
        padding: 40px;
        border-radius: 20px;
        background-color: var(--heading-bg);
        
        @include max (1199px){
            margin-left: 50px;
        }
        @include max (991px){
            padding: 30px;
            margin-left: 0;
            margin-top: 40px;
        }
        @include max (767px){
            padding: 30px 24px;
            margin-top: 30px;
        }
        h3{
            color: var(--light-color);
            padding-bottom: 15px;
        }
        p{
            color: var(--light-color);
            margin-bottom: 40px;
        }
    } 
    &-map {
        width: 100%;
        height: 750px;
        @include max (991px){
            height: 400px;
        }
    }
}
.pb-100{
    padding-bottom: 100px;
    @include max (991px){
        padding-bottom: 80px;
    }
    @include max (767px){
        padding-bottom: 60px;
    }
}