.frado-about{
    &-content{
        padding: 100px 0 130px;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
        span{
            color: rgba(24, 29, 20, 0.5);
        }
    }
    // end one
    &-title{
        p{
            font-size: 24px;
            line-height: 32px;
            max-width: 950px;
        }
    }
    &-slider-init{
        padding-top: 80px;
        @include max (767px){
            padding-top: 60px;
        }
    }
    &-thumb{
        padding: 0 12px;
    }
    &-thumb2{
        @include max (991px){
            img{
                width: 100%;
            }
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
        img{
            border-radius: 20px;
        }
    }
    &-data{
        padding-bottom: 24px;
        h4{
            padding-bottom: 15px;
            color: var(--light-color);
        }
    }
}
.frado-video{
    &-wrap{
        position: relative;
        z-index: 1;
        @include max (991px){
            text-align: center;
            padding-top: 40px;
        }
        @include max (767px){
            text-align: center;
            padding-top: 30px;
        }
    }
}
.play-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include max (991px){
        top: 58%;
    }
}

.frado-popup-video{
    animation: animName 8s linear infinite;
    display: block;
}
@keyframes animName {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}