    }
    &-table-title{
        flex: 0 0 36%;
    }
    &-table-body{
        flex: 0 0 44%;
        padding: 0 0 0 80px;
        @include max (1399px){
            padding: 0;
        }
        @include max (1399px){
            padding: 0 0 0 50px;
        }
        @include max (991px){
            padding: 15px 0;
        }
    }
    &-table-btn{
        flex: 0 0 20%;
        display: flex;
        justify-content: end;
        @include max (991px){
            justify-content: left;
        }
    }
    // end one
    &-section2{
        padding: 130px 0;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
    }
    &-table-item-wrap{
        display: flex;
        align-items: start;
        padding: 24px;
        border-bottom: 1px solid rgba(24, 29, 20, 0.1);
        position: relative;
        @include max (991px){
            display: block;
        }
        @include max (991px){
            padding-bottom: 24px;
            border-bottom: 1px solid rgba(24, 29, 20, 0.1);
            margin-bottom: 0;
        }
        &:last-child{
            border-bottom: none;
        }
        @include max (991px){
            display: block;
        }
    }
    &-table-icon{
        flex: 0 0 20%;
       
    }
    &-table-body2{
        flex: 0 0 50%;
        h3{
            padding-bottom: 15px;
        }
        @include max (991px){
            padding: 15px 0;
        }
    }
    &-table-date{
        flex: 0 0 30%;
        display: flex;
        justify-content: end;
        @include max (991px){
            justify-content: left;
        }
    }
}




.frado-content{
    &-thumb{
        @include max (991px){
            img{
                width: 100%;
            }
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
}