.frado-faq{
  &-item{
    margin-bottom: 24px;
    transition: all 0.4s;
    &.open {
      .frado-faq-body{
          display: block;
        }
        .frado-active-icon{
          transform: rotate(180deg);
        }
    }
    &.item2{
      border-radius: 20px;
      padding: 30px;
      background-color: var(--light-color);
      box-shadow: 0 4px 50px rgba(0, 0, 0, 0.25);
     
      @include max (767px){
        padding: 30px 24px;
      }
    }
  }
  &-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    gap: 15px;
    span{
      margin-right: 10px;
      color: #04DA00;
    }
  }
  &-body{
    display: none;
    padding-right: 70px;
    padding-top: 10px;
  }
  &-thumb{
    @include max (991px){
      padding-bottom: 40px;
    }
    @include max (767px){
      padding-bottom: 30px;
    }
    img{
      border-radius: 20px;
      @include max (991px){
        width: 100%;
      }
    }
  }
  // add iner page
  &-title{
    position: relative;
    margin: 0 auto;
    text-align: center;
    max-width: 900px;
    p{
      font-size: 24px;
      line-height: 32px;
      max-width: 890px;
      text-align: center;
      margin: 0 auto;
    }
  }
}





img.up {
  opacity: 0;
  position: absolute;
}
img.down {
  opacity: 1;
  position: relative;
}
.frado-faq-item.item2.open {
  img.up{
    opacity: 1!important;
    transform: rotate(180deg);
  }
  img.down{
    opacity: 0;
    display: block;
  }

}

.frado-active-icon{
  flex-shrink: 0;
  transition: all 0.4s;
  img{
    @include max (767px){
      width: 50px;
    }
  }
}

.tabs{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 24px;
  flex-wrap: wrap;
  margin-bottom: 80px;
  @include max (991px){
    margin-bottom: 60px;
  }
  @include max (767px){
    margin-bottom: 40px;
  }
  li{
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    padding: 19px 43px;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.4s;
    border: 2px solid var(--heading-color);
    &.current{
      border: 2px solid var(--heading-color);
      color: var(--light-color);
      background-color: var(--heading-color);
    }
  }
}


.frado-tab-content{
	display: none;

}

.frado-tab-content.current{
	display: inherit;
}