.frado-section-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.frado-section-padding2{
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}
.frado-section-padding3{
    padding: 30px 0 106px;
    @include max (991px){
        padding: 30px 0 76px;
    }
    @include max (767px){
        padding: 30px 0 36px;
    }
}
.frado-section-padding4{
    padding: 30px 0 130px;
    @include max (991px){
        padding: 0 0 80px;
    }
    @include max (767px){
        padding: 0 0 60px;
    }
}
.frado-section-padding5{
    padding: 30px 0 0;
}
.pb-96{
    padding-bottom: 96px;
    @include max (991px){
        padding-bottom: 56px;
    }
    @include max (767px){
        padding-bottom: 36px;
    }
}

// section title

.frado-section-title{
    padding-bottom: 50px;
    @include max (991px){
        padding-bottom: 40px;
        text-align: center;
    }
    @include max (767px){
        padding-bottom: 30px;
    }
    &.max-680{
        max-width: 680px;
    }
    &.max-750{
        max-width: 750px;
    }
    &.title2{
        @include max (991px){
            text-align: left;
        }
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
        &.max-width-750{
            max-width: 750px;
        }
    }
}

.frado-section-title2{
    position: relative;
    p{
        font-size: 24px;
        line-height: 32px;
        margin-bottom: 0;
    }
    &.center{
        max-width: 1070px;
        text-align: center;
        margin: 0 auto;
        &.max-900{
            max-width: 900px;
        }
    }
}
.max-950{
    max-width: 950px;
    margin: 0 auto;
    text-align: center;
    font-size: 24px;
    line-height: 32px;
}
// default content
.frado-default-content{
    h2{
        padding-bottom: 20px;
    }
    h4{
        padding-bottom: 15px;
    }
    &.content2{
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &.content3{
        padding-left: 95px;
        h2, p {
            color: var(--light-color);
        }
        @include max (1199px){
            padding-left: 50px;
        }
        @include max (991px){
            padding-left: 0;
        }
    }
    &.pl-100{
        padding-left: 100px;
    }
}
.title{
    padding-bottom: 20px;
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.section{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.pr-50{
    padding-right: 50px;
    @include max (991px){
        padding-right: 0;
    }
}
.pr-70{
    padding-right: 70px;
    @include max (1199px){
        padding-right: 30px;
    }
    @include max (991px){
        padding-right: 0;
    }
}
.pl-50{
    padding-left: 50px;
    @include max (991px){
        padding-left: 0;
    }
}
.plll-50{
    @include max (1399px){
        padding-left: 50px;
    }
    @include max (1199px){
        padding-left: 30px;
    }
    @include max (991px){
        padding-left: 0;
    }
}