.frado-p{
    &-box{
        padding-top: 50px;
        margin-bottom: 24px;
        @include max (1199px){
            padding-top: 40px;
        }
        @include max (991px){
            padding: 30px 30px 0;
        }
        @include max (767px){
            padding: 30px 20px 0;
        }
    }
    &-wrap{
        transition: all 0.4s;
        position: relative;
    }
    &-content{
        margin-top: 50px;
        @include max (1199px){
            margin-top: 40px;
        }
        @include max (991px){
            margin-top: 30px;
        }
        h4{
            padding-bottom: 15px;
        }
    }
    &-btn{
        margin-top: 50px;
        @include max (1199px){
            margin-top: 40px;
        }
        @include max (991px){
            margin-top: 30px;
        }
    }
    &-thumb{
        @include max (991px){
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
        img{
            border-radius: 20px 0 0 0;
            @include max (991px){
                border-radius: 20px 20px 0 0;
            }
            width: 100%;
        }
        &.thumb2{
            @include max (991px){
                padding-top: 0!important;
                padding-bottom: 40px!important;
            }
            @include max (767px){
                padding-top: 0!important;
                padding-bottom: 30px!important;
            }
        }
    }
    // end one
    &-wrap2{
        border-radius: 20px;
        margin-bottom: 24px;
        background-color: var(--light-color);
        &:hover{
            .frado-p-thumb2 img{
                transform: scale(1.1);
            }
            .frado-p-btn2{
                opacity: 1;
            }
        }
        &.wrap3{
            margin: 0 12px;
        }
    }
    &-thumb2{
        position: relative;
        overflow: hidden;
        display: block;
        border-radius: 20px 20px 0 0;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 20px 20px 0 0;
        }
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            background-color: rgba(0, 0, 0, 0.25);
            opacity: 0;
            transition: all 0.4s;
        }
        &:hover::before{
            opacity: 1;
        }
    }
    &-content2{
        padding: 24px 30px 30px 30px;
        @include max (767px){
            padding: 20px;
        }
    }
    &-btn2 {
        position: absolute;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
        width: 80px;
        height: 80px;
        background-color: var(--accent-bg);
        display: flex;
        transition: all 0.4s;
        align-items: center;
        justify-content: center;
        border-radius: 100px;
        z-index: 3;
        opacity: 0;
        a{
            padding: 25px;
        }
        svg{
            transform: rotate(-45deg);
        }
        @include max (991px){
            width: 60px;
            height: 60px;
        }
    }
    &-tag{
        margin-top: 24px;
        display: flex;
        align-items: center;
        gap: 12px;
        flex-wrap: wrap;
    }
    // add inner page
    &-d-wrap{
        max-width: 1080px;
        margin: 0 auto;
    }
    &-d-thumb{
        padding-top: 80px;
        @include max (757px){
            padding-top: 60px;
        }
    }
    &-d-content
    h3{
        padding-bottom: 15px;
    }
    &-d-data{
        h4{
            padding-bottom: 12px;
        }
    }
    &-d-sidebar{
        position: sticky;
        top: 110px;
        padding: 30px;
        border-radius: 20px;
        transition: all .4s;
        background-color: var(--heading-bg);
        margin-left: 30px;
        @include max (991px){
            margin-left: 0;
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
        ul{
            li{
                position: relative;
                padding-left: 20px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                h4, p{
                    color: var(--light-color);
                }
                &::before{
                    width: 12px;
                    height: 12px;
                    top: 10px;
                    left: 0;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--accent-color);
                }
            }
        }
    }
}

.pt-24{
    padding-top: 24px;
}

.frado-portfolio-menu{
    ul {
        display: flex;
        gap: 24px;
        flex-wrap: wrap;
        align-items: center;
        @include max (991px){
            justify-content: center;
        }
    }
}