// fonts variable
$body-font: 'Satoshi', serif!default;
$heading-font: 'Clash Grotesk', serif!default;

// color variable
:root {
  --heading-color: #181D14;
  --heading-bg: #181D14;
  --accent-color: #04DA00;
  --accent-bg: #04DA00;
  --white-color: #ffffff;
  --white-bg: #ffffff;
  --light-color: #F3EEE3;
}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 20px;
$buton_line_height: 0.67em;
$buton_border_radius: 50px;
$button_padding: 21px 28px;
$button_font_weight: 500;
$button_font_family: $body-font;